export const addItemToCart = (cartItems, cartItemToAdd) => {
	const existingCartItem = cartItems.find(
		cartItems => cartItems.id === cartItemToAdd.id
	)
	if (existingCartItem) {
		return cartItems.map(cartItems =>
			cartItems.id === cartItemToAdd.id ?
				{...cartItems, quantity: cartItems.quantity + 1}
				: cartItems
		)
	}
	return [...cartItems,{...cartItemToAdd, quantity:1}]
}

export const removeItemFromCart = (cartItems, cartItemToRemove) => {
	const existingCartItem = cartItems.find(
		cartItem => cartItem.id === cartItemToRemove.id
	);
	
	if (existingCartItem.quantity === 1) {
		return cartItems.filter(cartItem => cartItem.id !== cartItemToRemove.id);
	}
	
	return cartItems.map(cartItem =>
		cartItem.id === cartItemToRemove.id
			? {...cartItem, quantity: cartItem.quantity - 1}
			: cartItem
	);
};

export const getCartItems = cartItems =>cartItems.reduce(
	(accumalatedQuantity, cartItem) =>
		accumalatedQuantity + cartItem.quantity,
	0
)

export const getTotal = cartItems=> cartItems.reduce(
	(accumalatedPrice, cartItem) =>
		accumalatedPrice + cartItem.price*cartItem.quantity,
	0
)